export default {
	data() {
		return {
			show: false,
			isshow: true,
			islist: false,
			isactive: '',
		}
	},
	methods: {
		opendetail(){
			this.isshow = !this.isshow;
		},
		open(){
			this.show = true;
			this.isactive = ''
		},
		nav(index){
			this.islist = true;
			this.isactive = index;
		},
		open1(){
			this.islist = false
		}
	}
}
